@import url('https://fonts.googleapis.com/css?family=Raleway:400,600,700&display=swap');

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Raleway', sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.main-title {
  text-align: center;
  margin-top: 2rem;
  font-size: 2rem;
}

#travel-section {
  padding: 20px;
  width: 100%; /* Full width */
  max-width: 1200px; /* Max width for large screens */
  margin: auto; /* Center align the section */
}

.container {
  display: grid;
  grid-template-columns: repeat(3, 2fr); /* 3 items per row */
  grid-gap: 1rem; /* Space between the grid items */
  padding: 0;
  list-style: none;
}

.content {
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  transition: transform 0.3s ease-in-out;
  position: relative; /* Position relative for absolute child positioning */
}

.content:hover {
  transform: scale(1.03); /* Scale up on hover */
}

.content-image {
  width: 100%;
  height: auto; /* Maintain aspect ratio */
  display: block;
}

.content-details {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  padding: 20px;
  color: #fff;
  opacity: 0; /* Start invisible */
  transition: opacity 0.3s ease-in-out;
}

.content:hover .content-details {
  opacity: 1; /* Show details on hover */
}

.content-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0.5rem 0;
}

.content-text {
  font-size: 1rem;
  margin-bottom: 1rem;
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: repeat(2, 2fr); /* 2 items per row on smaller screens */
  }
}

@media (max-width: 480px) {
  .container {
    grid-template-columns: 1fr; /* 1 item per row on very small screens */
  }
}
