/* Core Styles for the Centered Navbar */
.navbar {
  background-color: #f8f9fa; /* Soft background color */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Enhanced shadow for depth */
  padding: 1rem 2rem; /* Balanced padding for aesthetics */
  display: flex; /* Flex display to handle inner elements */
  justify-content: center; /* Center everything in the navbar */
  align-items: center; /* Center items vertically */
  flex-wrap: wrap; /* Allow items to wrap as needed */
  position: relative; /* Setup for absolute positioned elements */
  z-index: 5; /* Ensure navbar is above other content */
}

/* Center-aligned Hamburger Menu for a Cool Effect */
.menu-toggle {
  cursor: pointer;
  font-size: 2rem; /* Increased icon size for better interaction */
  color: #333; /* Icon color */
  display: none; /* Hidden by default, shown in responsive */
  position: absolute; /* Positioned absolutely for manual placement */
  right: 20px; /* Right alignment for access */
  top: 50%; /* Vertically center */
  transform: translateY(-50%); /* Offset by half its height for perfect centering */
  z-index: 10; /* Ensure it's above the expanded menu */
}

/* Navigation List Centered */
.nav-list {
  list-style: none; /* Remove default list styling */
  display: flex; /* Inline display for desktop */
  gap: 2rem; /* Spacious gap between items */
  padding: 0; /* Reset default padding */
  margin: 0; /* Reset default margin */
  transition: all 0.5s ease; /* Smooth transition for any changes */
  justify-content: center; /* Center the list items */
  width: 100%; /* Full width to accommodate centering */
}

/* Navigation Item for a Clean, Centered Look */
.nav-list li {
  transition: transform 0.3s ease; /* Smooth transition for hover effects */
}

/* Dynamic and Centered Navigation Links */
.nav-list li a {
  padding: 0.75rem 1.5rem; /* Comfortable padding */
  font-size: 1rem; /* Readable font size */
  font-weight: 600; /* Medium font weight for emphasis */
  color: #333; /* Text color */
  background: linear-gradient(45deg, #bed7ef, #e9ecef); /* Gradient background */
  border-radius: 5px; /* Rounded corners for a modern feel */
  transition: all 0.3s ease; /* Smooth transition for hover effects */
  display: block; /* Block display to enable padding and background effects */
  text-decoration: none; /* Remove underline */
}

/* Responsive Adjustments for Centered Display */
@media (max-width: 768px) {
  .menu-toggle {
    display: block; /* Show menu toggle on small screens */
  }

  .nav-list {
    position: absolute;
    top: 100%; /* Align directly below the navbar */
    left: 0;
    right: 0; /* Ensure full width coverage */
    display: none; /* Hide by default */
    flex-direction: column; /* Stack items vertically */
    background-color: #f8f9fa; /* Match navbar background */
    box-shadow: 0 8px 10px rgba(0, 0, 0, 0.1); /* Shadow for depth */
    border-radius: 0 0 10px 10px; /* Rounded bottom corners */
    align-items: center; /* Center-align items for a cohesive look */
  }

  .nav-list.active {
    display: flex; /* Display flex to show the menu */
    padding: 1rem 0; /* Padding top and bottom for spacing */
  }

  .nav-list li {
    width: 100%; /* Full width for list items */
    text-align: center; /* Center text within items */
  }
}
