#education {
  background: #f8f9fa; /* Light grey background for a softer look */
  color: #4f80b0; /* Dark text for a professional look */
  padding: 40px 0;
  font-family: 'Poppins', sans-serif;
}

#education h2 {
  text-align: center;
  font-size: 2.5rem;
  color: #73abe2; /* Keeping the color consistent with the text */
  margin-bottom: 2rem;
}

.timeline-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding: 0 20px;
}

.timeline-block {
  background: #ffffff; /* White background for the blocks */
  border-left: 4px solid #b0dfff; /* Vibrant blue for a pop of color */
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05); /* Lighter shadow for a softer look */
  max-width: 300px;
  position: relative;
  transition: transform 0.3s ease-in-out;
}

.timeline-block:hover {
  transform: translateY(-10px); /* Lift effect on hover */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.08); /* Slightly deeper shadow on hover for more depth */
}

.timeline-dot {
  height: 20px;
  width: 20px;
  background-color: #3498db; /* Maintaining the vibrant blue dot */
  border-radius: 50%;
  position: absolute;
  left: -10px; /* Adjusted to match the new design */
  top: 30px;
}

.timeline-date {
  font-size: 0.9rem;
  color: #97b7d3; /* Dark grey for better readability */
  margin-bottom: 15px;
}

.timeline-content h3 {
  font-size: 1.25rem;
  color: #2c3e50; /* Dark color for headings */
  margin-top: 0.5rem;
  margin-bottom: 0.25rem;
}

.timeline-content p {
  color: #2c3e50; /* Dark color for consistent text */
}

@media (max-width: 767px) {
  .timeline-container {
    flex-direction: column;
    align-items: center;
  }

  .timeline-block {
    max-width: 90%;
    margin: 0 auto;
  }

  .timeline-dot {
    left: 50%;
    transform: translateX(-50%) translateY(-50%); /* Adjusting the dot to be centered properly */
  }
}
