/* Improved Google Font import for performance and visual diversity */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&family=Source+Code+Pro:wght@400;600&display=swap');

/* Root CSS variables for theme consistency and easy adjustments */
:root {
  --primary-color: #5b4b8a; /* Updated to a deeper purple for sophistication */
  --secondary-color: #2d3436; /* Darker grey for stronger contrast */
  --accent-color: #e17055; /* Warm orange for engaging calls to action */
  --background-color: #ffffff; /* Maintained clean white for freshness */
  --code-background-color: #eef0f1; /* Softer background for code blocks */
  --code-text-color: #b07219; /* Adjusted for better readability */
  --font-family: 'Poppins', sans-serif;
  --code-font-family: 'Source Code Pro', monospace;
}

/* Base styles */
body {
  margin: 0;
  font-family: var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--background-color);
  color: var(--secondary-color);
}

code {
  font-family: var(--code-font-family);
  background-color: var(--code-background-color);
  color: var(--code-text-color);
  padding: 4px 6px;
  border-radius: 4px;
  box-shadow: inset 0 0 4px rgba(0,0,0,0.1); /* Enhanced shadow for depth */
}

/* Enhanced example element for visual interest */
.example-element {
  background-color: var(--primary-color);
  color: #ffffff;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0,0,0,0.1);
  transition: all 0.3s ease; /* Smooth transition for hover effects */
}

.example-element:hover {
  box-shadow: 0 8px 16px rgba(0,0,0,0.2);
  transform: translateY(-2px); /* Slight raise effect on hover */
}

/* Responsive container */
.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 40px; /* Increased padding for better spacing */
}

/* Spacing utilities refinement */
.mt-20, .mb-20, .pt-20, .pb-20 {
  margin-top: 20px;
  margin-bottom: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
}

/* Typography adjustments for visual hierarchy */
h1, h2, h3, h4, h5, h6 {
  color: var(--secondary-color);
  margin-top: 20px;
  margin-bottom: 15px; /* Adjusted for visual rhythm */
}

h1 { font-size: 3rem; /* Increased for impact */ }
h2 { font-size: 2.5rem; }
h3 { font-size: 2rem; }
h4 { font-size: 1.75rem; }
h5, h6 { font-size: 1.5rem; }

/* Mobile responsiveness */
@media (max-width: 768px) {
  .container {
    padding: 20px;
  }

  h1 { font-size: 2.25rem; }
  h2 { font-size: 2rem; }
  h3, h4, h5, h6 { font-size: 1.75rem; }
}
