/* ProjectsTimeline.css */

#projects-timeline {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Poppins', sans-serif;
}

#projects-timeline h1 {
  text-align: center;
  margin-bottom: 2rem;
  color: #2c3e50; /* A dark shade for the title */
}

#projects-timeline ul {
  list-style: none;
  padding: 0;
  position: relative;
}

#projects-timeline ul::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: #3498db; /* Blue color for the line */
  transform: translateX(-50%);
}

#projects-timeline .project-item {
  position: relative;
  width: calc(50% - 10px); /* Subtracting the gap from the width */
  background: white;
  margin: 1rem 0;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-left: 4px solid var(--accent-color);
  border-radius: 4px;
  --accent-color: #41516C; /* Declare the CSS variable inside the specific selector */
  border-left: 4px solid var(--accent-color);
}

#projects-timeline .project-item:nth-child(odd) {
  left: 0;
}

#projects-timeline .project-item:nth-child(even) {
  left: 50%;
  border-left: none;
  border-right: 4px solid var(--accent-color);
  
}

#projects-timeline .project-item::before {
  content: '';
  position: absolute;
  top: 20px;
  left: -9px;
  height: 18px;
  width: 18px;
  background-color: white;
  border: 4px solid var(--accent-color);
  border-radius: 50%;
  z-index: 1;
}

#projects-timeline .project-item:nth-child(even)::before {
  left: auto;
  right: -9px;
}

#projects-timeline .project-item .date {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--accent-color);
  margin-bottom: 0.5rem;
}

#projects-timeline .project-item .title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 0.5rem;
}

#projects-timeline .project-item .descr {
  font-size: 1rem;
  color: #666;
  line-height: 1.5;
}

@media screen and (max-width: 768px) {
  #projects-timeline ul::before {
    left: 20px;
    transform: translateX(0);
  }

  #projects-timeline .project-item {
    width: 100%;
    margin-left: 40px;
  }

  #projects-timeline .project-item:nth-child(even) {
    left: 0;
    border-right: none;
  }

  #projects-timeline .project-item::before,
  #projects-timeline .project-item:nth-child(even)::before {
    left: -29px;
  }
}
