/* Updated AboutMe.css with New Color Palette and Increased Width */

#about-me.card {
  background: #fefef7; /* Adjust if you have a specific background color in mind */
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: row; /* Corrected comment to match the layout */
  align-items: center;
  gap: 2rem; /* Increased gap for better spacing */
  padding: 2rem;
  max-width: 1000px; /* Increased width for more space */
  margin: 2rem auto;
}

.card-avatar {
  width: 250px; /* Adjusted avatar size for balance */
  height: 200px; /* Made the avatar more square to match increased size */
  border-radius: 20%;
  background-size: cover;
  background-position: center; /* Center the image for better focus */
}

.card-info {
  text-align: center;
}

.card-title {
  font-size: 2.5rem;
  color: #223; /* Consider using navbar's text color if different */
}

.card-subtitle {
  font-size: 1.25rem;
  color: #555; /* Adjust based on navbar's secondary text color */
}

.about-me-content p {
  font-size: 1rem;
  color: #666; /* Ensure readability against the white background */
  line-height: 1.5;
  margin-bottom: 1rem;
}

.card-social .card-social__list {
  display: flex; /* Changed to flex for consistent spacing */
  flex-wrap: wrap; /* Allows items to wrap on smaller screens */
  justify-content: center; /* Centers items horizontally */
  list-style: none;
  gap: 1rem; /* Utilizes gap property for spacing */
  margin-top: 1rem;
}

.card-social .card-social__item a {
  color: #3498db; /* Accent color, possibly match navbar's highlight color */
  font-size: 1.5rem;
}
.card-social .card-social__item a {
  position: relative;
  display: inline-block; /* Ensure the link encloses the icon and text properly */
}
.card-social .card-social__item a span {
  position: absolute;
  bottom: 100%; /* Position the name above the icon */
  left: 50%; /* Center align the name horizontally with the icon */
  transform: translateX(-50%) translateY(-10px); /* Adjust horizontal centering and spacing */
  white-space: nowrap; /* Prevent the name from wrapping */
  visibility: hidden; /* Hide the name by default */
  opacity: 0;
  transition: opacity 0.3s, visibility 0.3s, transform 0.3s; /* Smooth transition for the hover effect */
  background-color: #333; /* Background color of the tooltip */
  color: #fff; /* Text color of the tooltip */
  text-align: center;
  border-radius: 4px;
  padding: 5px 8px; /* Padding around the text */
  font-size: 0.75rem; /* Smaller font size for the tooltip text */
  z-index: 1; /* Ensure the tooltip is above other content */
}
.card-social .card-social__item a:hover span,
.card-social .card-social__item a:focus span {
  visibility: visible; /* Show the name on hover/focus */
  opacity: 1;
  transform: translateX(-50%) translateY(-15px); /* Adjust for a slight upward move on hover */
}

.card-social .card-social__item a:hover {
  color: #2c3e50; /* Darker shade for hover effect */
}

.card-social .card-social__item img {
  width: 24px;
  height: 24px;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  #about-me.card {
    flex-direction: column; /* Stack elements vertically on smaller screens */
    padding: 1rem;
    max-width: 95%; /* Allow for more responsive width on very small screens */
  }

  .card-avatar {
    width: 150px; /* Adjusted for smaller screens */
    height: 150px;
    margin: 0 auto; /* Center avatar when stacked */
  }

  .card-title, .card-subtitle {
    font-size: 1.75rem; /* Adjusted title size for mobile */
  }
}
