#skills-section {
  background: #f8f9fa; /* Softened light gray for a clean, modern background */
  color: #34495e; /* Updated to a slightly deeper blue for sharper contrast */
  padding: 50px; /* Increased padding for more space */
  text-align: center; /* Center alignment for titles and content */
}

#skills-section h2 {
  color: #34495e; /* Consistent with the section's text color */
  margin-bottom: 2.5rem; /* More space below the heading */
  font-size: 2.75rem; /* Slightly larger for emphasis */
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Maintains responsive grid */
  gap: 30px; /* Increased gap for better spacing */
  justify-content: center; /* Centered content within the grid */
}

.skill-card {
  background: linear-gradient(145deg, #ffffff, #e6e9ef); /* Retain the subtle gradient */
  border-radius: 10px; /* Keep the modern rounded corners */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1); /* Maintain the smooth shadow */
  padding: 15px; /* Reduced padding for tighter content */
  display: flex;
  flex-direction: column;
  justify-content: space-around; /* Adjust if necessary to space-between for more spread */
  align-items: center; /* Central alignment for content */
  min-height: 220px; /* Adjust height as needed to fit content */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Smooth interaction effects */
}

.skill-card:hover {
  transform: translateY(-10px); /* Lift effect on hover for dynamism */
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.12); /* Deeper shadow for emphasis */
}

.skill-card h3 {
  color: #34495e; /* Consistent dark text color */
  margin-bottom: 0.5rem; /* Decreased space below headings */
  font-weight: bold;
}

.skill-card p {
  font-size: 1rem; /* Standard readable size */
  color: #7d8a97; /* Soft contrast for readability */
  margin-bottom: 0.8rem; /* Reduced bottom margin for tighter layout */
}

.skill-card .fa-icon {
  margin-bottom: 1rem; /* Less space above the icon description */
  color: #47a8bd; /* Eye-catching blue */
  font-size: 2.5rem; /* Large icon for visual impact */
}


@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr; /* Single column layout on smaller screens */
    gap: 20px; /* Adjusted gap for consistency */
  }

  .skill-card {
    width: auto; /* Flexibility in width adjustment */
  }
}
